.post-item {
	display: grid;
	grid-gap: 15px;
	padding: 2px 2px 0 2px;
}

.post-item:hover {
	box-shadow: -20px 20px 0 -17px #eee,
	20px -20px 0 -17px #eee,
	20px 20px 0 -20px #929292,
	0 0 0 2px #929292;
	transform: scale(1.02);
	transition: .25s cubic-bezier(0.47,0,0.75,0.72);
}
.posts-list .post-item:nth-child(even) {
	direction: rtl;
}

.post-description {
	display: grid;
	row-gap: 5px;
}
.post-info {
	display: flex;
	align-items: center;
	margin-top: -18px;
}

.post-date {
	width: 45px;
	height: 45px;
	background-color: #b68d55;
	color: white;
	text-align: center;
	font-size: 14px;
	padding: 8px;
}
.post-category {
	text-transform: uppercase;
	font-size: 14px;
	font-weight: bold;
	letter-spacing: 0.2em;
	color: #b68d55;
	padding-left: 15px;
}
.posts-list .post-item:nth-child(even) .post-category {
	padding-right: 15px;
}

.post-author {
	font-style: italic;
	color: #cccccc;
	font-size: 14px;
	padding-left: 10px;
	padding-right: 15px;
}
.post-title {
	background-color: white;
	font-weight: bold;
	padding:15px 0;
	font-size: 18px;
}

.post-text {
	line-height: 1.5em;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -moz-box;
	-moz-box-orient: vertical;
	display: -webkit-box;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	line-clamp: 4;
	box-orient: vertical;
}
.posts-list .post-item:nth-child(even) .post-text {
	text-align: left;
	padding-left: 10px;
	direction: ltr;
}
.posts-list .post-item:nth-child(even) .link-read-more {
	justify-self: left;
	padding-left: 10px;
}


@media(max-width: 767px) {
	.post-item {
		max-width: 300px;
	}
}

@media (min-width:768px) and (max-width:991px) {
	.post-item {
		max-width: 350px;
	}
}

@media(min-width: 992px) {

	.post-item {
		grid-template-columns:1fr 1fr;
	}
	.post-info {
		margin-top: 0;
		margin-left: -60px;
	}
	.post-title {
		padding:15px 10px;
		margin-left: -60px;
		max-width: 110%;
	}
	.posts-list .post-item:nth-child(even) .post-info {
		margin-right: -60px;
	}
	.posts-list .post-item:nth-child(even) .post-title {
		margin-right: -60px;
	}
}