.category-row {
	display: grid;
	grid-auto-rows: min-content;
	grid-template-columns:1fr;
	grid-gap: 15px;
	grid-template-areas: 
		"category-title"
		"posts-list"
	;
}

.category-title {
	grid-area: category-title;
	font-size: 22px;
    font-weight: bold;
    justify-self: center;
    text-transform: uppercase;
	padding-top: 60px;
}
.category-title::before {
	content: "—";
	color: rgb(182, 142, 83);
	padding-right: 20px;
}
.category-title::after {
	content: "—";
	color: rgb(182, 142, 83);
	padding-left: 20px;
}

.categ-posts-list {
	grid-area: posts-list;
	display: grid;
	row-gap: 30px;
	justify-items: center;
}

.categ-post-item {
	display: grid;
	grid-gap: 15px;
	padding: 5px;
	max-width: 320px;
}
.categ-post-item:hover {
	box-shadow: -20px 20px 0 -17px #eee,
	20px -20px 0 -17px #eee,
	20px 20px 0 -20px #929292,
	0 0 0 2px #929292;
	transform: scale(1.02);
	transition: .25s cubic-bezier(0.47,0,0.75,0.72);
}

.categ-post-photo>img {
	width: 100%;
}
.categ-post-description {
	display: grid;
	row-gap: 5px;
	justify-items: center;
}
.categ-post-text {
	line-height: 1.5em;
	padding: 0 5px;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden; 
}

.post-like.liked.hover {
	cursor: unset;
}
@media(min-width: 768px) {
	
	.category-row {
		grid-template-columns:1fr;
		column-gap: 15px;
		row-gap: 65px;
		grid-template-areas:
			"category-title"
			"posts-list"
		;
	}
	.categ-posts-list {
		grid-template-columns: 1fr 1fr;
    	column-gap: 10px;
		grid-template-areas:
			"post-item post-item" 
		;
	}
}

@media(min-width: 992px) {
	
	.categ-posts-list {
		grid-template-columns: 1fr 1fr 1fr;
    	column-gap: 10px;
		grid-template-areas:
			"post-item post-item post-item" 
		;
	}
}
