.blog-posts-list {
	grid-area: posts-list;
	display: grid;
	row-gap: 30px;
	justify-items: center;
}

.blog-post-item {
	display: grid;
	grid-gap: 15px;
	padding: 5px;
	max-width: 320px;
}
.blog-post-item:hover {
	box-shadow: -20px 20px 0 -17px #eee,
	20px -20px 0 -17px #eee,
	20px 20px 0 -20px #929292,
	0 0 0 2px #929292;
	transform: scale(1.02);
	transition: .25s cubic-bezier(0.47,0,0.75,0.72);
}

.blog-post-photo>img {
	width: 100%;
}
.blog-post-description {
	display: grid;
	row-gap: 5px;
	justify-items: center;
}

.blog-post-text {
	line-height: 1.5em;
}








@media(min-width: 992px) {
	
	.blog-posts-list {
		grid-template-columns: 1fr 1fr;
    	column-gap: 10px;
		grid-template-areas:
			"post-item post-item" 
			"post-item post-item" 
		;
	}
}