.post-header-info {
	display: flex;
	align-items: center;
	margin-top: -18px;
	justify-content: space-between;
}

.post-header-date {
	width: 45px;
	height: 45px;
	background-color: #b68d55;
	color: white;
	text-align: center;
	font-size: 14px;
	padding: 8px;
}
.post-header-category {
	text-transform: uppercase;
	font-size: 14px;
	font-weight: bold;
	letter-spacing: 0.2em;
	color: #b68d55;
}
.post-header-author {
	font-style: italic;
	color: #cccccc;
	font-size: 14px;
}
.post-header-title {
	background-color: white;
	font-weight: bold;
	padding:15px 5px 0 5px;
	font-size: 18px;
}