.instagram-block {
	overflow-x: hidden;
	padding: 30px 0;
}

.inst-title {
	font-size: 18px;
    font-weight: bold;
    letter-spacing: 0.2em;
	text-align: center;
	padding: 40px 0;
}

.cards-slider-wrapper {
	display: flex;
	position: absolute;
	transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.cards-slider-wrapper .card {
	flex:1;
	min-width: 200px;
	opacity: 0.5;
	transform: scale(0.7);
	transition: opacity 300ms linear, transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.cards-slider.active-slide-0 #card-0, .cards-slider.active-slide-1 #card-1, .cards-slider.active-slide-2 #card-2, .cards-slider.active-slide-3 #card-3,.cards-slider.active-slide-4 #card-4, .cards-slider.active-slide-5 #card-5, .cards-slider.active-slide-6 #card-6, .cards-slider.active-slide-7 #card-7, .cards-slider.active-slide-8 #card-8, .cards-slider.active-slide-9 #card-9, .cards-slider.active-slide-10 #card-10, .cards-slider.active-slide-11 #card-11, .cards-slider.active-slide-12 #card-12 {
	opacity: 1;
	transform: scale(1);
}
.cards-slider.active-slide-1 #card-1{
	opacity: 1;
}
.cards-slider {
	position: relative;
	max-width: 200px;
	margin: 0 auto;
	height: 200px;
	padding-top: 5px;
}
.cards-slider::after {
	content:'';
	display: block;
	width: 100%;
	height: 200px;
	outline: 5px solid #b68e53;
	position: absolute;
	top:5px;
	left:0;
}

.button-box {
	display: flex;
    justify-content: center;
    margin-top: 40px;
    height: 50px;
}
.button-box button {
	width: 130px;
	height: 40px;
	background: white;
	border: 1px solid #b68e53;
	box-shadow: 0px 1px 0 black, 2px 4px 5px black;
	font-weight: 900;
	letter-spacing: 1px;
	-webkit-transition: all 150ms linear;
	transition: all 150ms linear;
	color: #b68e53;
	outline: unset;
}
.button-box button:first-child {
	margin-right: 25px;
}

.button-box button:hover {
	border: 1px solid rgba(0, 0, 0, 0.2);
	box-shadow: 1px 1px 2px rgba(255, 255, 255, 0.2);
	text-shadow: 1px 1px 0 black;
	-webkit-transition: all 250ms linear;
	transition: all 250ms linear;
	cursor: pointer;
}
.button-box button:disabled {
	border: 1px solid rgba(0, 0, 0, 0.2);
	box-shadow: 1px 1px 2px rgba(255, 255, 255, 0.2);
	text-shadow: 1px 1px 0 black;
	-webkit-transition: all 250ms linear;
	transition: all 250ms linear;
	color: grey;
	cursor: unset;
}
