.categories-section {
	display: grid;
	row-gap: 25px;
	justify-items: center;
}
.categories-list>li {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px;
}
.categories-list>li:hover a {
	color: #b08247;;
}
.categories-list>li>a {
	display: block;
	text-decoration: none;
	color: #b5b5b5;
}
.categories-list>li>div {
	font-size: 12px;
	padding-left: 10px;
}