
.about-section {
	display: grid;
	row-gap: 25px;
	justify-items: center;
}
.photo-block {
	display: flex;
	justify-content: center;
	float: left;
}
.photo-block>img {
	border-radius: 50px;
	margin-right: 15px;
}
.text-block {
	font-style: italic;
	color:#6f6f6f;
	font-size: 17px;
	line-height: 1.4em;
	text-align: justify;
}
.link-more-about-us {
	text-decoration: unset;
	color:grey;
}

.react-tabs {
	width: calc(100% - 70px);
}
.react-tabs__tab {
	color:grey;
}
.react-tabs__tab-panel {
	padding: 10px;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
}
.react-tabs__tab-list {
	margin: 0;
	border-bottom: unset;
}
.react-tabs__tab--selected {
	box-shadow: inset 2px 2px 2px 0 hsl(0deg 0% 100% / 50%), 4px -2px 5px 0 rgb(0 0 0 / 25%);
	border-color: transparent;
	color: black;
}
.react-tabs__tab-panel--selected {
	box-shadow: inset 2px 2px 2px 0 hsl(0deg 0% 100% / 50%), 7px 7px 20px 0 rgb(0 0 0 / 25%), 4px 4px 5px 0 rgb(0 0 0 / 25%);
}
.more-about-us-block {
	height: 100px;
	line-height: 100px;
	text-align: center;
}
.more-about-us-block a {
	color: black;
}