.post-page-row {
	display: grid;
	grid-auto-rows: min-content;
	grid-template-columns:1fr;
	grid-gap: 15px;
	grid-template-areas: 
		"post"
		"posts-sidebar"
	;
}
.post {
	grid-area: post;
}

.post-article-title {
	font-weight: bold;
	font-size: 24px;
}

.post-article-photo {
	display: flex;
    flex-direction: column;
	padding: 5px;
}
.post-article-photo img {
	width: 100%;
}
.post-article-info {
	display: flex;
	align-items: center;
}

.post-full-text {
	padding: 15px;
	line-height: 1.5em;
	text-align: justify;
	text-indent: 1.5em;
}
.post-full-text h2 {
	padding-bottom: 10px;
	font-size: 19px;
	font-weight: bold;
	color: #b68e53;
	text-indent: 2.5em;
}
.post-full-text h3 {
	font-size: 18px;
	font-weight: bold;
	font-style: italic;
}
.post-full-text p {
	padding-bottom: 15px;
	text-shadow: 2px 8px 6px rgb(0 0 0 / 10%), 0px -5px 35px rgb(255 255 255 / 10%);
	font-size: 17px;
}
.post-full-text p span {
	color:#b68e53;
	font-weight: bold;
}

.pictures-block {
	display: flex;
	justify-content: space-between;
	margin: 20px 0;
	padding: 0 15px;
	flex-wrap: wrap;
	width: 100%;
}
.pictures-block>img {
	width:calc(100% - 5px);
	margin-bottom: 10px;
}
.italic-font {
	font-style: italic;
}
.bolt-font {
	font-weight: bold;
}
.title-like-box {
	display: flex;
	border-bottom: 1px solid grey;
	margin: 0 15px;
	justify-content: space-between;
	align-items: center;
	height: 80px;
}

@media(min-width: 768px) {

	.post-article-photo {
		float: left;
		padding: 15px 15px 0 15px;
	}

	.post-page-row {
		grid-template-columns:60% 40%;
		column-gap: 10px;
		row-gap: 65px;
		grid-template-areas:
			"post posts-sidebar"
		;
	}
	
}

@media(min-width: 992px) {

	.post-page-row {
		grid-template-columns:70% 30%;
		column-gap: 15px;
		row-gap: 65px;
		grid-template-areas:
			"post posts-sidebar"
		;
	}
	.pictures-block>img {
		width:calc(50% - 5px);
	}
}