* {
	box-sizing: border-box;
}
body {
	font-family: 'Inter', sans-serif;
}
.main {
	display: grid;
	z-index: 150;
	background-color: white;
}
.container {
	max-width: 100%;
	margin: 0 auto;
}
.header {
	position: sticky;
	top:-90px;
	z-index: 200;
	background-color: white;
	padding-bottom: 22px;
}
.header-row {
	display: grid;
	row-gap: 20px;
	grid-template-areas:
		"logo logo"
		"search-field menu"
	;
}
.footer {
	padding-top: 100px;
	z-index: 100;
	background-color: white;
}
.footer-row {
	display: grid;
	row-gap:35px;
	justify-items: center;
}

.sticky-picture {
	position: sticky;
	top:90px;
	z-index: -20;
}
.sticky-picture>img {
	width: 100%;
	max-height: 450px;
}
.title-block {
	font-weight: bold;
	letter-spacing: 0.2em;
	justify-self: center;
}
.title-block:before {
    content: "—";
	color: #b68e53;
	padding-right: 20px;
}
.title-block:after {
    content: "—";
	color: #b68e53;
	padding-left: 20px;
}
.link-read-more {
	text-decoration: none;
	color: #b68e53;
	font-weight: bold;
	width: max-content;
	padding: 5px;
}
.link-read-more:hover {
	color: black;
}
.post-like {
	width: 30px;
    height: 30px;
	background-repeat: no-repeat;
	background-position-x: center;
	background-position-y: center;
	background-image: url(unlike.png);
}
.post-like:hover {
    cursor: pointer;
}
.liked {
	background-image: url(like.png);
}

@media(max-width: 767px) {
	.header-row {
		justify-items: center;
		grid-template-columns: 80% 20%;
		align-items: center;
	}
}

@media(min-width: 768px) {
	.container {
		width: 750px;
	}
	.header-row {
		grid-template-columns: 60% 40%;
		grid-template-areas:
			"logo logo"
			"menu search-field"
		;
	}
	
}

@media(min-width: 992px) {
	.container {
		width: 970px;
	}
	
}
@media(min-width: 1200px) {
	.container {
		width: 1170px;
	}
}