.comments-section {
	display: grid;
	row-gap: 25px;
	justify-items: center;
	padding-top: 70px;
}
.comments-card {
	display: grid;
	row-gap: 10px;
	padding: 0 15px;
	width: 100%;
}

.user-info {
	display: flex;
	color: grey;
	align-items: center;
}
.user-comment-logo {
	width: 35px;
	height: 35px;
	background-image: url(user_icon.png);
	background-position-x: center;
	background-position-y: center;
	background-repeat: no-repeat;
}
.user-comment-name {
	font-weight: bold;
	padding: 0 15px 0 5px;
}
.user-comment-text {
	text-align: justify;
	line-height: 1.1em;
}
.leave-comment {
    color: #7f8c8c;
    font-size: 20px;
    font-weight: bold;
    padding-top: 20px;
    margin-top: 80px;
}
.leave-comment-card {
   display: grid;
   row-gap: 15px;
   width: 100%;
   padding: 0 15px;
}
.leave-comment-form {
   display: grid;
   row-gap: 15px;
}
.leave-comment-form input {
	width: 100%;
	background-color:#e5e6e6;
	border-radius: 4px;
	height: 40px;
	border-color:transparent;
	padding: 0 10px;
}
.leave-comment-form span {
	color:#7f8c8c; 
}
.leave-comment-form-message {
	height: 200px;
	background-color:#e5e6e6;
	border-radius: 4px;
	width: 100%;
	border-color:transparent;
	padding: 10px;
}
.leave-comment-form .send {
	width: 170px;
    height: 40px;
    background: #fff;
    border: 1px solid #b68e53;
    box-shadow: 0 1px 0 #000, 2px 4px 5px #000;
    font-weight: 900;
    letter-spacing: 1px;
    transition: all .15s linear;
    color: #b68e53;
	justify-self: center;
	outline: unset;
}
.leave-comment-form .send:hover {
	border: 1px solid rgba(0,0,0,.2);
    box-shadow: 1px 1px 2px hsl(0deg 0% 100% / 20%);
    text-shadow: 1px 1px 0 #000;
    transition: all .25s linear;
    cursor: pointer;
}

@media(min-width: 768px) and (max-width:1199px) {

	.user-info {
		flex-wrap: wrap;
	}
	.user-comment-date {
		padding-top: 10px;
	}
}